* {
  margin: 0;
  padding: 0;
}

$base-color: antiquewhite;

body {
  background-color: $base-color !important;
}

.shortenForm {
  display: flex;
  flex-direction: column;
  width: 80%;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 15%;
  & > div {
    margin: 2rem 0;
  }
  .shortenButton {
    width: 40%;
    align-self: center;
  }
  .shortUrlInput {
    margin-top: 5rem;
    margin-bottom: 0;
  }
  .copyAlert {
    max-width: 150px;
    color: rgb(54, 49, 49);
    background-color: antiquewhite;
    border-radius: 1rem;
    align-self: flex-end;
    opacity: 0.8;
    height: 45px;
    margin-top: 0;
  }

  .circularProgress {
    align-self: center;
  }
}

.bgMenu {
  cursor: pointer;
  margin-left: 1rem;
}

.footer {
  position: absolute;
  font-family: cursive;
  bottom: 0;
  width: 100%;
  height: 100px;
  background: #57534e;
  color: antiquewhite;
  font-size: 1.1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  div {
    display: flex;
    width: 20%;
    justify-content: center;
    .ftIcon {
      align-self: center;
    }
    > * {
      margin-left: 10px;
    }
    @media only screen and (max-width: 992px) {
      width: 57%;
      font-size: 0.9rem;
      > * {
        margin-left: 5px;
      }
    }
  }
}

a {
  text-decoration: none;
}

.menu {
  .MuiPaper-root {
    background-color: whitesmoke !important;
  }
  a {
    color: inherit;
  }
}

.profileWithPicture {
  display: flex;
  padding: 1rem;
  margin: 1rem;
  background-color: #d4d4d8;

  .profile-info {
    .spanParent {
      display: flex;
      border-radius: 0.5rem;
      flex-direction: column;
      opacity: 0.7;
      margin-bottom: 1rem;
      padding: 0.5rem 1rem;
      border: 1px solid rgb(107, 89, 89);
      background-color: #3a3937;
      span:first-child {
        color: #e9ddce;
        height: 20px;
        font-style: italic;
        font-size: 0.9rem;
      }
      span:last-child {
        color: rgb(17, 144, 167);
        font-size: 1.2rem;
        font-weight: 600;
      }
    }
  }
  @media only screen and (max-width: 492px) {
    margin: 0;
    padding: 0;
    // margin-top: 2rem;
    background-color: antiquewhite;
    .spanParent {
      background-color: antiquewhite !important;
      border: none !important;
      span:first-child {
        color: #33312f !important;
      }
      span:last-child {
        color: rgb(17, 144, 167);
        font-size: 1.2rem;
        font-weight: 600;
      }
    }
  }
}
